import React from 'react'
import logo from '../logo.png'
import "./Compiler/Home.css";

// import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
export default function Navbar(props) {
	return (
		<>
			<nav className="navbar navbar-expand-lg navbar-dark bg-dark">
				<img className=" logos  " id='logo' src={logo} alt={`c-logo`} />
				<button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
					<span className="navbar-toggler-icon"></span>
				</button>

				<div className="collapse navbar-collapse" id="navbarSupportedContent">
					<ul className="navbar-nav mr-auto">
						<li className="nav-item active">
							<Link className="nav-link active" to="/Online-Code-Editor">Home <span className="sr-only">(current)</span></Link>
						</li>
						<li className="nav-item">
							<Link className="nav-link active" to="/about">About us</Link>
						</li>
						<li className="nav-item">
							<Link className="nav-link active" to="/contact">Contact us</Link>
						</li>
						<li className="nav-item">
							<Link className="nav-link active" to="/compiler">IDE</Link>
						</li>
						<li className="nav-item">
							<Link className="nav-link active" to="/help">Help</Link>
						</li>
					</ul>
					{/* <form className="form-inline my-2 my-lg-0">
						<input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" />
						<button className="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
					</form> */}
				</div>
			</nav>
		</>
	)
}

