import React from 'react';
import "./Home.css";
import { Link } from 'react-router-dom';
import logo from "../../logo1.svg";


export default function OnlineCodeEditor() {
    return (
        <>
            <head>
                <meta name="viewport" content="width=device-width, initial-scale=1" />
                <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.4.1/css/bootstrap.min.css" />
                <script src="https://ajax.googleapis.com/ajax/libs/jquery/3.6.0/jquery.min.js"></script>
                <script src="https://maxcdn.bootstrapcdn.com/bootstrap/3.4.1/js/bootstrap.min.js"></script>

            </head>
            <div className="had">
                <div className='text-center text-white text '> <h1>Welcome to Online Code Editor</h1></div>
                <Link to="/compiler" role="button">

                    <div className='logos-v mx-auto d-block my-5'>
                        <img src={logo} className=" rounded mx-auto d-block logo-h" alt='mlogo' />

                    </div>
                    <div className='col text-center'>
                        <button className="btn btn-warning text-center">Start your Coding Journey</button>
                    </div>
                </Link>
            </div>

        </>
    );
}
